/**
 * 
 */
import $ from 'jquery';
// https://nodejs.org/api/esm.html
import svgPathParser from 'svg-path-parser';
const parseSVG = svgPathParser.parseSVG;
const makeAbsolute = svgPathParser.makeAbsolute;

/**
 * 
 * @param pt1
 * @param pt2
 */
function ptEstimatedDist(pt1, pt2) {
    // x,y
    // return (pt1.x - pt2.x) ** 2 + (pt1.y - pt2.y) ** 2;
    // 
       return Math.abs(pt1.x - pt2.x, 2) + Math.abs(pt1.y - pt2.y, 2);
}

/**
 * 
 * @param pt 
 * @param rect 
 */
function isPtInRect(pt, rect) {
    return pt.x >= rect.left && pt.x <= rect.right && pt.y >= rect.top && pt.y <= rect.bottom;
}

/**
 * 
 * @param rect
 * @param pt
 */
function rectPtEstimatedDist(rect, pt) {
    const dist = [
        ptEstimatedDist(pt, {
            x: rect.left,
            y: rect.top,
        }),
        ptEstimatedDist(pt, {
            x: rect.left,
            y: rect.bottom,
        }),
        ptEstimatedDist(pt, {
            x: rect.right,
            y: rect.top,
        }),
        ptEstimatedDist(pt, {
            x: rect.right,
            y: rect.bottom,
        }),
    ];
    return Math.min.apply(null, dist);
}

/**
 * 
 * @param rect1
 * @param rect2
 */
function isRectIntersect(rect1, rect2) {
    const h = rect1.left >= rect2.left && rect1.left <= rect2.right
        || rect1.right >= rect2.left && rect1.right <= rect2.right
        || rect1.left <= rect2.left && rect1.right >= rect2.right;
    const v = rect1.top >= rect2.top && rect1.top <= rect2.bottom
        || rect1.bottom >= rect2.top && rect1.bottom <= rect2.bottom
        || rect1.top <= rect2.top && rect1.bottom >= rect2.bottom;

    return v && h;
}

/**
 * 
 */
export class SectionFlowViewer {
    constructor() {
        // dom
        this.parentDom = null;

        // 
        this.stationSet = new Set();
        // keyvaluesvg
        this.stationNameToGraph = new Map();
        // keyvaluesvg
        this.stationToUpSeg = new Map();
        // keyvaluesvg
        this.stationToDownSeg = new Map();

        // 
        // keyStart-End"-"valuesvg2[0]g[1][g]
        this.stationPairToUpSeg = new Map();
        // 
        // keyStart-End"-"valuesvg2[0]g[1][g]
        this.stationPairToDownSeg = new Map();
    }

    /**
     * svg
     * @param parent svgdom
     * @param svgPath  svg
     * @param opt <svg/>attribute
     */
    load(parent, svgPath, opt) {
        this.parentDom = parent;
        return new Promise((resolve, reject) => fetch(
            svgPath, {
                method: 'GET',
                dataType: 'json',
            },
        ).then((res) => {
            if (res.ok) {
                res.text().then((data) => {
                    $(this.parentDom).append(data);
                    this.setSvgAttributes(opt);
                    this.linkSvgGraph();
                    this.resetSectionsColor();
                    resolve(this);
                }).catch((e) => {
                    reject(e);
                });
            }
            else {
                // 
                res.text().then((data) => {
                    $(this.parentDom).html(data);
                });
                reject(res);
            }
        }));
    }

    /**
     * svgattribute
     * @param opt {width:'',height:'',viewBox:'xx,xx xx,xx xx,xx'}
     */
    setSvgAttributes(opt) {
        const $svg = $(this.parentDom).find('svg');
        if (opt && opt.width) {
            $svg.attr('width', opt.width);
        }
        if (opt && opt.height) {
            $svg.attr('height', opt.height);
        }
        if (opt && opt.viewBox) {
            $svg.attr('viewBox', opt.viewBox);
        }
        if (opt && opt.preserveAspectRatio) {
            $svg.attr('preserveAspectRatio', opt.preserveAspectRatio);
        }
    }


    buildMapKey(start, end) {
        return `${start}-${end}`;
    }

    /**
     * svg
     */
    linkSvgGraph() {
        // 
        const stationNameTexts = $(this.parentDom).find('#Station_Name > text');
        // console.log(stationNameTexts);
        stationNameTexts.each((_, stationText) => {
            this.parseStation(stationText);
        });
        this.linkStartEnd();
    }

    /**
     * stationToUpSegstationPairToUpSeg
     * stationToDownSegstationPairToDownSeg
     */
    linkStartEnd() {
        function setIntersect(sectionSet1, sectionSet2) {
            return [...new Set([...sectionSet1].filter(x => sectionSet2.indexOf(x) !== -1))];
        }

        this.stationSet.forEach((start) => {
            this.stationSet.forEach((end) => {
                let intersect = null;
                if (start === end) {
                    return;
                }
                intersect = setIntersect(
                    this.stationToUpSeg.get(start),
                    this.stationToUpSeg.get(end),
                );
                if (intersect.length > 0) {
                    this.stationPairToUpSeg.set(this.buildMapKey(start, end), intersect);
                }

                intersect = setIntersect(
                    this.stationToDownSeg.get(start),
                    this.stationToDownSeg.get(end),
                );
                if (intersect.length > 0) {
                    this.stationPairToDownSeg.set(this.buildMapKey(start, end), intersect);
                }
            });
        });
    }

    /**
     * 
     * @param stationText svg
     */
    parseStation(stationText) {
        const nameRect = stationText.getBoundingClientRect();
        // 50%
        const nameRectFilter = {
            left: nameRect.left - nameRect.width / 2,
            right: nameRect.right + nameRect.width / 2,
            top: nameRect.top - nameRect.height / 2,
            bottom: nameRect.bottom + nameRect.height / 2,
        };
        nameRectFilter.width = nameRectFilter.right - nameRectFilter.left;
        nameRectFilter.height = nameRectFilter.bottom - nameRectFilter.top;
        nameRectFilter.x = nameRectFilter.left;
        nameRectFilter.y = nameRectFilter.top;

        let minDist = Number.MAX_VALUE;
        let minStation = null;
        // 
        const allStations = $(this.parentDom).find('#Station > use');
        allStations.each((index, station) => {
            const stationRect = station.getBoundingClientRect();
            if (isRectIntersect(stationRect, nameRectFilter)) { // 
                // 
                const dist = rectPtEstimatedDist(nameRect, {
                    x: (stationRect.left + stationRect.right) / 2,
                    y: (stationRect.top + stationRect.bottom) / 2,
                });
                if (minDist > dist) {
                    minDist = dist;
                    minStation = station;
                }
            }
        });

        // 
        // ie11get(0)textContent
        const stationName = $(stationText).get(0).textContent;
        this.stationSet.add(stationName);
        this.stationNameToGraph.set(stationName, minStation);
        // 
        const upSetions = this.linkSection(minStation, true);
        this.stationToUpSeg.set(stationName, upSetions);
        const downSections = this.linkSection(minStation, false);
        this.stationToDownSeg.set(stationName, downSections);
    }


    /**
     * 
     * @param station 
     * @param isUP true
     */
    linkSection(station, isUP) {
        const svg = $(this.parentDom).find('svg')[0];

        // 
        const stationRect = station.getBoundingClientRect();

        // 
        const sections = [];
        if (isUP) {
            // 
            const allUpSections = $(this.parentDom).find('#Section_Up g');
            allUpSections.each(this.test.bind(this, svg, stationRect, sections));
        }
        else {
            // 
            const allDownSections = $(this.parentDom).find('#Section_Down g');
            allDownSections.each(this.test.bind(this, svg, stationRect, sections));
        }
        return sections;
    }

    /**
     * svg
     * @param svg svg
     * @param stationRect 
     * @param svgElement svg
     * @param x svgx
     * @param y svgy
     */
    isSvgPointInStationRect(svg, stationRect, svgElement, x, y) {
        const pt = svg.createSVGPoint();
        pt.x = x;
        pt.y = y;
        const screenP = pt.matrixTransform(svgElement.getScreenCTM());
        return isPtInRect(screenP, stationRect);
    }

    /**
     * svgline
     * @param validatePoint
     * @param line
     * @param rect
     */
    isLineIntersectRect(validatePoint, line, rect) {
        return isRectIntersect(line.getBoundingClientRect(), rect)
            && (validatePoint(line, $(line).attr('x1'), $(line).attr('y1'))
                || validatePoint(line, $(line).attr('x2'), $(line).attr('y2')));
    }

    /**
     * svgpath
     * @param validatePoint
     * @param path
     * @param rect
     */
    isPathIntersectRect(validatePoint, path, rect) {
        let isIn = false;
        if (isRectIntersect(path.getBoundingClientRect(), rect)) {
            const d = $(path).attr('d');
            const commands = parseSVG(d);
            makeAbsolute(commands); // Note: mutates the commands in place!
            $(commands).each((_, command) => {
                if (validatePoint(path, command.x, command.y)) {
                    isIn = true;
                    return false;
                }
                return true;
            });
        }
        return isIn;
    }

    /**
     * svgpolygon
     * @param validatePoint
     * @param polygon
     * @param rect
     */
    isPolygonIntersectRect(validatePoint, polygon, rect) {
        if (isRectIntersect(polygon.getBoundingClientRect(), rect)) {
            const points = $(polygon).attr('points').split(' ');
            for (let i = 0; i < points.length; i += 2) {
                if (validatePoint(polygon, points[i], points[i + 1])) {
                    return true;
                }
            }
        }
        return false;
    }

    /**
     * 
     * @param svg
     * @param stationRect
     * @param result
     * @param _
     * @param section
     */
    test(svg, stationRect, result, index, section) {
        if (!isRectIntersect(section.getBoundingClientRect(), stationRect)) {
            return;
        }

        const svgElements = [];
        $(section).find('line').each((_, item) => {
            svgElements.push(item);
        });
        $(section).find('path').each((_, item) => {
            svgElements.push(item);
        });
        $(section).find('polygon').each((_, item) => {
            svgElements.push(item);
        });

        const isSvgPointValid = this.isSvgPointInStationRect.bind(this, svg, stationRect);
        $(svgElements).each((_, element) => {
            let isIn = false;
            isIn = isIn || element.tagName === 'line' && this.isLineIntersectRect(isSvgPointValid, element, stationRect);
            isIn = isIn || element.tagName === 'path' && this.isPathIntersectRect(isSvgPointValid, element, stationRect);
            isIn = isIn || element.tagName === 'polygon' && this.isPolygonIntersectRect(isSvgPointValid, element, stationRect);
            if (isIn) {
                result.push(section);
                return false;
            }
            return true;
        });
    }

    /**
     * 
     * @param section svgsvgpathlinepolygon
     * @param color 
     * @param start 
     * @param end 
     * @param isUp 
     */
    changeSectionColor(start, end, isUp, color) {
        const key = this.buildMapKey(start, end);
        if (isUp) {
            const sections = this.stationPairToUpSeg.get(key);
            this.changeSection(sections, color);
        }
        else {
            const sections = this.stationPairToDownSeg.get(key);
            this.changeSection(sections, color);
        }
    }

    /**
     * 
     */
    changeSection(section, color) {
        $(section).find('path').each((index, path) => {
            $(path).css('stroke', color);
            if ($(path).attr('d').endsWith('Z')) { // 
                $(path).css('fill', color);
            }
        });
        $(section).find('line').css('stroke', color);
        $(section).find('polygon').css('fill', color);
        $(section).find('polygon').css('stroke', color);
    }

    /**
     * 
     */
    resetSectionsColor(color = 'white') {
        for (const sectionArray of this.stationPairToUpSeg.values()) {
            sectionArray.forEach(section => this.changeSection(section, color));
        }
        for (const sectionArray of this.stationPairToDownSeg.values()) {
            sectionArray.forEach(section => this.changeSection(section, color));
        }
    }

    /**
     * 
     * @param opt  {upColor:'1',downColor:'2'}//upColordownColor
     */
    showLinkResult(opt) {
        // x
        const sortedStationNames = Array.from(this.stationSet);
        sortedStationNames.sort((a, b) => {
            const aRect = this.stationNameToGraph.get(a).getBoundingClientRect();
            const bRect = this.stationNameToGraph.get(b).getBoundingClientRect();
            return aRect.left < bRect.left ? -1 : 1;
        });

        let timeout = 0;
        for (let i = 0; i < sortedStationNames.length; i++) {
            const start = sortedStationNames[i];
            for (let j = i + 1; j < sortedStationNames.length; j++) {
                const end = sortedStationNames[j];
                const up = this.stationPairToUpSeg.get(this.buildMapKey(start, end));
                const down = this.stationPairToDownSeg.get(this.buildMapKey(start, end));
                // 
                if (up) {
                    timeout += 500;
                    setTimeout(() => {
                        up.forEach(section => this.changeSection(section, opt.upColor));
                    }, timeout);
                }
                // 
                if (down) {
                    timeout += 500;
                    setTimeout(() => {
                        down.forEach(section => this.changeSection(section, opt.downColor));
                    }, timeout);
                }
            }
        }
    }

    /** GET
     * @param geturl geturl
    */
    fetchGet(geturl) {
        return fetch(geturl, {
            method: 'GET',
            dataType: 'json',
        }).then((res) => {
            res.json().then((response) => {
                return response;
            }, (error) => {
            return error;
            });
        });
    }

    /** POST
     * @param posturl posturl
     * @param params postdata(String)
    */
    fetchPost(posturl, params) {
        const paramsstr = JSON.stringify(params);
        return fetch(posturl, {
            method: 'POST',
            dataType: 'json',
            body: paramsstr,
            headers: new Headers({
                Accept: 'application/json',
                'Content-Type': 'text/plain;charset=UTF-8',
            }),
        }).then((res) => {
            return res.json();
        });
    }
}
export default SectionFlowViewer;
