import $ from 'jquery';
import SectionFlowViewer from 'section-flow-viewer';// 
import flowConfig from './flow/flowconfig.mjs';

const {
    APIhost, svgViewBox, svgName, sourceUrl, imgName,
} = flowConfig;
const viewer = new SectionFlowViewer();
export default function () {
    // svg
    let svgurl = '';
    const svgdata = {
        fileType: 4,
    };
    // svgurl
    viewer.fetchPost(`${APIhost}/acc/monitor/sectionpassengerflow/getResourceURL`, svgdata)
        .then((res) => {
            console.log(res);
            if (res.retCode === 0) {
                svgurl = `${res.URL}${svgName}`;
                init(svgurl);
            }
        }, (error) => {
            console.log(error);
        });
    /**
     * svgurlSVG
     * @param svgurlstr SVGurl
     */
    function init(svgurlstr) {
        viewer.load($('#svgContainer'), svgurlstr, svgViewBox).then((v) => {
            console.log('svg loaded');
            // 
            const sbdata1 = {
                lineIds: [1],
            };
            viewer.fetchPost(`${APIhost}/acc/monitor/sectionpassengerflow/getSectionPassengerFlowInit`, sbdata1)
                .then((data) => {
                    const initResData = data;// 
                    console.log(initResData);
                    let initDataArr = [];
                    if (initResData.retCode === 0) {
                        initDataArr = initResData.data;
                        $('#Background').children('image').eq(0).attr('xlink:href', `${sourceUrl}${imgName}`);
                    }
                    // hover
                    showModal(initDataArr, true);
                    showModal(initDataArr, false);
                    // timespace
                    const timespace = 2000;
                    setInterval(() => getCountData(initDataArr), timespace);
                    // getCountData(initDataArr);
                }, (error) => {
                    console.log(error);
                });// 
        }).catch((e) => {
            console.log(`Load failed: ${e}`);
        });

        /**
         * 
         * @param initDataArr 
         */
        function getCountData(initDataArr) {
            // timeIntervalId5
            const currentHour = new Date().getHours();
            const min1 = 12 * currentHour;
            const currentMinute = new Date().getMinutes();
            const yushu = (currentMinute % 5);
            const min2 = yushu === 0 ? parseInt(currentMinute / 5, 0) : parseInt(currentMinute / 5, 0) + 1;
            const minNumber = min1 + min2;
            // console.log(currentHour);
            // console.log('5', min1);
            // console.log('currentMinute', currentMinute);
            // console.log(yushu);
            // console.log('5', min2);
            // console.log(minNumber);
            const mydate = new Date();
            const year = mydate.getFullYear();
            const month = mydate.getMonth() + 1 > 9 ? mydate.getMonth() + 1 : `0${mydate.getMonth() + 1}`;
            const date0 = mydate.getDate() > 9 ? mydate.getDate() : `0${mydate.getDay()}`;
            const datestr = year.toString() + month.toString() + date0.toString();
            const sbdata2 = {
                lineIds: [1],
                date: datestr,
                timeIntervalId: minNumber,
                intervalCount: 1,
            };
            viewer.fetchPost(`${APIhost}/acc/monitor/sectionpassengerflow/getSectionPassengerFlow`, sbdata2)
                .then((countData) => {
                    // console.log(countData);
                    let countDataArr = null;
                    let lowLimit = null;
                    let upLimit = null;
                    if (countData.retCode === 0) {
                        countDataArr = countData.data;
                        lowLimit = countData.lowerLimit;
                        upLimit = countData.upperLimit;
                        showSVGClor(initDataArr, countDataArr, lowLimit, upLimit, true);
                        showSVGClor(initDataArr, countDataArr, lowLimit, upLimit, false);
                        showLineData(initDataArr, countDataArr, true);
                        showLineData(initDataArr, countDataArr, false);
                    }
                }, (error) => { console.log(error); });
        }

        /**
         * @param initDataArr :
         * @param countDataArr 
         * @param lowLimit 
         * @param upLimit 
         * @param isUp 
         */
        function showSVGClor(initDataArr, countDataArr, lowLimit, upLimit, isUp) {
            countDataArr.map((item) => {
                const totalCount = item.uCount + item.dCount;
                return initDataArr.forEach((dataitem) => {
                    if (dataitem.sectionId === item.sectionId) {
                        if (totalCount < lowLimit) { viewer.changeSectionColor(dataitem.pStation, dataitem.dStation, isUp, 'lightgreen'); }
                        else if (totalCount >= lowLimit && totalCount <= upLimit) { viewer.changeSectionColor(dataitem.pStation, dataitem.dStation, isUp, 'yellow'); }
                        else { viewer.changeSectionColor(dataitem.pStation, dataitem.dStation, isUp, 'red'); }
                    }
                });
            });
            // viewer.showLinkResult({upColor: 'red', downColor:'blue'});//  
        }

        /**
        * hover
        * @param initDataArr:
        * @param isUp
        */
        function showModal(initDataArr, isUp) {
            initDataArr.forEach((item, index) => {
                const modalDiv = '<div class ="dataContainer"><p class="downCount"></p><p class="upCount"></p><p class="totalCount"></p></div>';
                $('#svgContainer').append(modalDiv);
                $('.dataContainer').css({ background: 'white', width: '120px', height: '90px' });
                $('.downCount').css({ 'font-size': '12px', 'padding-left': '5px' });
                $('.upCount').css({ 'font-size': '12px', 'padding-left': '5px' });
                $('.totalCount').css({ 'font-size': '12px', 'padding-left': '5px' });
                const stationNameStr0 = `${item.pStation}-${item.dStation}`;
                let upSeg = null;
                // upSeg = isUp ? this.stationPairToUpSeg.get(stationNameStr0)[0] this.stationPairToDownSeg.get(stationNameStr0)[0];///
                upSeg = isUp ? viewer.stationPairToUpSeg.get(stationNameStr0) : viewer.stationPairToDownSeg.get(stationNameStr0);// /
                $(upSeg).attr('sectionId', item.sectionId).css({ cursor: 'pointer' });
                $(upSeg).mouseover((event) => {
                    // windowsEventxy
                    const limitX = parseInt($('#svgContainer').css('width'), 0) * 3 / 4;
                    const limitY = parseInt($('#svgContainer').css('height'), 0) * 2 / 3;
                    const dataContainerWidth = parseInt($('.dataContainer').eq(index).css('width'), 0);
                    const dataContainerheight = parseInt($('.dataContainer').eq(index).css('height'), 0);
                    const positionX = parseInt(event.clientX, 0) >= limitX ? parseInt(event.clientX, 0) - dataContainerWidth : parseInt(event.clientX, 0);
                    const positionY = parseInt(event.clientY, 0) >= limitY ? parseInt(event.clientY, 0) - dataContainerheight : parseInt(event.clientY, 0);
                    $('.dataContainer').eq(index).css({
                        display: 'block', position: 'absolute', left: `${positionX}px`, top: `${positionY}px`,
                    });
                }).mouseout(() => {
                    $('.dataContainer').eq(index).css({ display: 'none' });
                });
            });
        }

        /**
     * 
     * @param initDataArr: sectionId
     * @param countDataArrarr0arr1sectionId
     * @param isUp
     */
        function showLineData(initDataArr, countDataArr, isUp) {
            return initDataArr.forEach((dataitem, index) => {
                const stationNameStr0 = `${dataitem.pStation}-${dataitem.dStation}`;
                let upSeg = null;
                upSeg = isUp ? viewer.stationPairToUpSeg.get(stationNameStr0) : viewer.stationPairToDownSeg.get(stationNameStr0);// /
                countDataArr.forEach((item) => {
                    if (Number($(upSeg).attr('sectionId')) === item.sectionId) {
                        const downCount = item.dCount;
                        const upCount = item.uCount;
                        const totalCount = item.dCount + item.uCount;
                        $('.upCount').eq(index).html(`${upCount}`);
                        $('.downCount').eq(index).html(`${downCount}`);
                        $('.totalCount').eq(index).html(`${totalCount}`);
                    }
                });
            });
        }
    }
}
